.admin-panel {
  display: none;
  position: sticky;
  bottom: 0;
  width: 100%;
  padding: $container-padding;
  margin-left: -$container-padding;
  margin-right: -$container-padding;
  justify-content: space-between;
  flex-direction: column;
}


.tab-pane {
  display: none;
  flex-direction: row;
  justify-content: space-between;
}


.tab-pane.show {
  display: flex;
}


.column {
  display: flex;
  flex-direction: column;
}


body.show-admin-panel {
  .admin-panel {
    display: flex;
  }
}




