
blockquote blockquote {
  margin-left: 0em;
}

form {
  margin-bottom: 0px;
}


.nothumb {
  float: left;
  text-align: center;
  padding: 14px;
  margin: 0 20px 2px 20px;
  border: 3px dashed;
}

.reflink a {
  color: inherit;
  text-decoration: none;
}

.reflink a:hover {
  color: #800000;
}

.userdelete {
  float: right;
  text-align: center;
  white-space: nowrap;
}

.pagelist {
  max-width: 600px;
}

.admin {
  color: #800080;
  font-weight: normal;
}

.mod {
  color: #FF0000;
  font-weight: normal;
}

.vip {
  color: #336600;
  font-weight: normal;
}

.reflinkpreview {
  position: absolute;
  padding: 5px;
  background-color: #F0E0D6;
  border: 1px dotted #000000;
}

.catalogtable {
  text-align: center;
}

.pgstbl table {
  border:1px solid;
}

.thrdcntnr {
  border-bottom:1px solid ;
  padding: 5px 0 15px 0;
  display:block;
  min-height: 210px;
}

span [id*="unhidethread"]{
  border-bottom:1px solid ;
  }
  
.highlight {
    width: auto;
    display:table;
    margin-top: 4px;
  min-width: 380px;
}

.catalogthread {
  vertical-align: top;
    word-wrap: break-word;
    overflow: hidden;
    display: inline-block;
    padding: 4px;
    width: 210px;
    max-height: 350px;
    min-height: 200px;
    margin-top: 5px;
    position: relative;
}

.catalogthread a {
  text-decoration: none;
}

.cataloglist { 
  padding: 20px 0px;
  text-align: center;
  border-bottom:1px solid ;
}

.closed {
  text-decoration: line-through;
}

.sticked {
  border: 1px dotted;
}

.inline-pp, .prettyprint {
  white-space: pre-wrap;
}

.threadlinksbottom {
  text-align: right;
}


.sound {
  float: left;
  padding-right: 20px;
  padding-bottom: 2px;
}



